<template>
    <div class="relative h-screen flex w-full vx-row no-gutter items-center justify-center bg_pale">
      <div class="md:absolute logo w-full">
        <router-link to="/">
        <img src="@/assets/images/logo/logo1.png" alt="로고" width="100">
        </router-link>
      </div>
      <div class="vx-col sm:w-full md:w-1/2 lg:w-1/3 xl:w-1/3 sm:m-3 m-4">
        <vx-card>
                <div slot="no-body" class="full-page-bg-color">
                    <div class="vx-row no-gutter">
                        <div class="vx-col sm:w-full md:w-full lg:w-full mx-auto self-center d-theme-dark-bg">
                            <div class="px-8 py-8 register-tabs-container">
                                <div class="vx-card__title mb-4">
                                    <h4 class="mb-4">회원가입</h4>
                                    <p>새 계정을 만들기 위해 아래의 양식을 작성해주세요.</p>
                                </div>
                                <vs-divider></vs-divider>
                                <div class="clearfix register">
                                  <vs-input
                                    name="email"
                                    type="email"
                                    label="Email"
                                    v-model="email"
                                    class="w-full" />

                                  <vs-input
                                    ref="password"
                                    type="password"
                                    name="password"
                                    label="Password"
                                    placeholder="Password"
                                    v-model="password"
                                    class="w-full mt-2" />
                                  <small class="text-primary">* 비밀번호는 8자 이상이어야 합니다.</small>
                                  <vs-input
                                    type="password"
                                    name="confirm_password"
                                    label="Confirm Password"
                                    placeholder="Confirm Password"
                                    v-model="confirm_password"
                                    class="w-full mt-2" />

                                  <vs-input
                                    label="certify_code"
                                    name="certify_code"
                                    placeholder="certify_code"
                                    v-model="certify_code"
                                    class="w-full mt-2"
                                    :disabled="!send_code" />

                                  <div class="flex items-center py-3">
                                    <vs-checkbox v-model="isTermsConditionAccepted" >
                                    I accept the terms & conditions.</vs-checkbox>
                                  </div>
                                  <vs-button class="mt-4" v-if="!send_code" @click="send_code=!send_code">인증번호 전송</vs-button>
                                  <vs-button class="float-right mt-6" v-else >가입</vs-button>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </vx-card>
        </div>
        
        <div class="terms">
          <ul>
            <li @click="termsPrompt=true"><a href="#">이용약관</a></li>
            <li @click="termsPrompt2=true"><a href="#">개인정보처리방침</a></li>
          </ul>
        </div>
        <vs-prompt
          class="position-relative"
          title="서비스 이용약관"
          buttonAccept="false"
          cancel-text = "닫기"
          button-cancel = "border"
          :active.sync="termsPrompt">
          <div class="mt-5">
            약관 내용
          </div>
        </vs-prompt>
        <vs-prompt
          class="position-relative"
          title="개인정보 처리방침"
          buttonAccept="false"
          cancel-text = "닫기"
          button-cancel = "border"
          :active.sync="termsPrompt2">
          <div class="mt-5">
            약관 내용
          </div>
        </vs-prompt>
    </div>
</template>

<script>
export default {
  data(){
    return{
      termsPrompt:false,
      termsPrompt2:false,
      certify_code: '',
      send_code: false,
      email: '',
      password: '',
      confirm_password: '',
      isTermsConditionAccepted: false
    }
  },
  computed: {
  },
  methods: {
  }
}
</script>

<style lang="scss" scoped>
.register-tabs-container {
  min-height: 517px;

  .con-tab {
    padding-bottom: 23px;
  }
}
.terms{
  position: absolute;
  bottom: 0;
  right:10px;
  margin-bottom: 40px;
  li{
    display: inline-block;
    margin-right: 20px;
  }
}
.logo{
  left:15px;
  top:15px;
}
@media screen and (max-width: 767px) {
    .h-screen{
      overflow-y: auto;
    }
    .register-tabs-container{
      position: static;
    }
    .terms{
      width: 100%;
      text-align: center;
      position: static;
      margin-top: 20px;
    }
    .terms li{
      margin-right: 0;
      padding: 0 10px;
    }
    
    .logo{
      padding-left:15px;
      padding-top:15px;
    }
}
</style>
<style>

.vs-con-input-label .vs-con-input{
  margin-top:5px;
}
</style>
